$navbarh: 6rem;
$open-navbarh: 18rem;
$max-pc-width: 160rem;
$tablet-width: 120rem;
$fixed-tablet-width: (120/1.6)+'rem';
$large-mobile-width: 80rem;
$fixed-large-mobile-width: (80/1.6)+'rem';
$med-mobile-width: 66rem;
$fixed-med-mobile-width: (66/1.6)+'rem';
$small-mobile-width: 48rem;
$fixed-small-mobile-width: (48/1.6)+'rem';
$tiny-mobile-width: 36rem;
$fixed-tiny-mobile-width: (36/1.6)+'rem';
$fast-animation: 0.4s;
$med-animation: 0.6s;
$slow-animation: 1s;
$fast-weather: 0.25s;
$med-weather: 0.69s;
$slow-weather: 1.25s;


#App {
	&.light-theme {
		@import './_LightTheme';
		@import './partials/_basics.scss';
		@import './partials/_elements.scss';
		@import './components/_appbar.scss';
		@import './components/_weathercards.scss';
		@import './components/clothingcards';
		@import './_App.scss';
	}
	&.dark-theme {
		@import './_DarkTheme.scss';
		@import './partials/_basics.scss';
		@import './partials/_elements.scss';
		@import './components/_appbar.scss';
		@import './components/_weathercards.scss';
		@import './components/clothingcards';
		@import './_App.scss';
	}
	height: 100%;
}

#App.nouser #WeatherDaily {
	width: 100% !important;
	max-width: 100rem;
	margin: 0 auto;
}